import {mapGetters} from 'vuex'

export default {
	computed: {
		//m_carttotal
		...mapGetters('m_cart', ['total'])
	},
	
	watch:{
		//total
		total(){
			this.setBadge()
		}
	},
	
	onShow() {
		//
		this.setBadge()
	},
	
	methods:{
		//
		setBadge(){
			uni.setTabBarBadge({
				index:2,   //tabbar 2tabbar
				text:this.total + ''   ,//text
			})
		}
	}
}